import React, { Component } from 'react'
import Card1 from '../assets/card1.png'
import Card2 from '../assets/card2.png'
import Card3 from '../assets/card3.png'
import Card4 from '../assets/cambio_privacidad.jpg'
import Card5 from '../assets/vista_aplicacion.jpg'
import DescriptionText from "./DescriptionText"

class CapturesCarousel extends Component {

  componentDidMount(){
    //Import here materialize-css because it uses window variable
    const M = require("materialize-css");
    var carousel = document.querySelectorAll('.carousel');
    M.Carousel.init(carousel, {"numVisible": 5, "shift": 10, "duration":125,"onCycleTo":this._onCarouselChange});
    var materialBoxed = document.querySelectorAll('.materialboxed');
    M.Materialbox.init(materialBoxed);
    var tabs = document.querySelectorAll('.tabs');
    M.Tabs.init(tabs, {"onShow":this._onTabChange}); 
    this._updateDimensions = this._updateDimensions.bind(this);
    if (window != null) {
      window.addEventListener("resize", this._updateDimensions);
    }
    
  }

  _onTabChange(e) {
    var carousel = document.querySelectorAll('.carousel');
    var carousel_instance = null;
    if(this.el.id === "mobile-tabs") {
      carousel_instance= M.Carousel.getInstance(carousel[1]);
    } else {
      carousel_instance = M.Carousel.getInstance(carousel[0]);
    }

    var carousel_center = null;
    var i = 0;
    carousel_instance.images.forEach(element => {
      if(element.className === "carousel-item active") {
        carousel_center = i;
        return;
      }
      i += 1
    });
    if (this.index !== carousel_center) {
      carousel_instance.set(this.index);
    }
  }

  _onCarouselChange(e) {
    console.log("aquí");
    var tabs = document.querySelectorAll('.tabs');
    var tab_instance = null;
    if(this.el.id === "mobile-carousel") {
      tab_instance= M.Tabs.getInstance(tabs[1]);
    } else {
      tab_instance = M.Tabs.getInstance(tabs[0]);
    }

    var carousel_center = null;
    var i = 0;
    this.images.forEach(element => {
      if(element.className === "carousel-item active") {
        carousel_center = i;
        return;
      }
      i += 1
    });
    //no se ha podido usar el method select() de materializeCSS porque no funcionaba con el edge
    if (tab_instance != null && tab_instance.index !== carousel_center && tab_instance.el.children.length-1 > carousel_center) {
        tab_instance.el.children[carousel_center].firstElementChild.click();
    }
    
  }

  _updateDimensions(e) {
    var carousel = document.querySelectorAll('.carousel');
    M.Carousel.init(carousel, {"numVisible": 5, "shift": 10, "duration":125,"onCycleTo":this._onCarouselChange});
  }

  render() {
      return (
        <>
          <div className="capturesCarousel row hide-on-med-and-down show-on-large">
            <div className="col s5 offset-s1">
              <ul className="tabs tabs-fixed-width tab-demo">
                  <li className="tab col s4"><a className="active waves-effect" href="#1"><i className="small material-icons">home</i></a></li>
                  <li className="tab col s4"><a className="waves-effect" href="#2"><i className="small material-icons">search</i></a></li>
                  <li className="tab col s4"><a className="waves-effect" href="#3"><i className="small material-icons">lock</i></a></li>
              </ul>
              <div id="1" className="col s12 no-scroll"><DescriptionText title={'presentation-title'} desc={'presentation-desc'} /></div>
              <div id="2" className="col s12 no-scroll"><DescriptionText title={'track-title'} desc={'track-desc'} /></div>
              <div id="3" className="col s12 no-scroll"><DescriptionText title={'anonymous-title'} desc={'anonymous-desc'} /></div>
            </div>
            <div className="col s6">
              <div className="carousel">
                <a className="carousel-item" href="#!"><img className="materialboxed z-depth-3" src={Card1} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="materialboxed z-depth-3" src={Card2} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="materialboxed z-depth-3" src={Card3} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="materialboxed z-depth-3" src={Card4} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="materialboxed z-depth-3" src={Card5} alt=""/></a>
              </div>
            </div>
          </div>
          <div id="mobile-capturesCarousel" className="capturesCarousel row show-on-medium-and-down hide-on-large-only">
            <div className="col s12" style={{zIndex: 2}}>
              <div id="mobile-carousel" className="carousel">
                <a className="carousel-item" href="#!"><img className="z-depth-3" src={Card1} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="z-depth-3" src={Card2} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="z-depth-3" src={Card3} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="z-depth-3" src={Card4} alt=""/></a>
                <a className="carousel-item" href="#!"><img className="z-depth-3" src={Card5} alt=""/></a>
              </div>
            </div>
            <div className="col s11 offset-s1">
              <ul id="mobile-tabs" className="tabs tabs-fixed-width tab-demo">
                  <li className="tab col s4"><a className="active waves-effect" href="#mob-1"><i className="small material-icons">home</i></a></li>
                  <li className="tab col s4"><a className="waves-effect" href="#mob-2"><i className="small material-icons">search</i></a></li>
                  <li className="tab col s4"><a className="waves-effect" href="#mob-3"><i className="small material-icons">lock</i></a></li>
              </ul>
              <div id="mob-1" className="col s12"><DescriptionText title={'presentation-title'} desc={'presentation-desc'} /></div>
              <div id="mob-2" className="col s12"><DescriptionText title={'track-title'} desc={'track-desc'} /></div>
              <div id="mob-3" className="col s12"><DescriptionText title={'anonymous-title'} desc={'anonymous-desc'} /></div>
            </div>
          </div>
        </>
      )
  }
}

/*
*/
          
export default CapturesCarousel

